html, body {margin: 0;height: 100%;}
a {text-decoration: none;}

/* 로고 영역 */
.navbar-brand {width: 200px;cursor: pointer;}

/* 레아웃 영역 */
.main_container, .sub_container {padding: 10px;box-sizing: border-box;}
.main_left, .sub_left {padding-right: 10px;}
.part_status_box {margin: 0 10px;}
.center_table {margin: 10px 0;}
/* .cal_box {padding: 1%;} */

/* 테이블 설정 */
.same_width {height: auto;text-align: center;table-layout: fixed;word-break: break-all;}

/* 기타 설정 */
.nav-link {font-size: 1rem !important;}
.main_table .MuiPaper-root {margin-top: 10px;}
.MuiCardHeader-root span {font-size: 1rem !important;}
.row {--bs-gutter-x: 1;}
table th, table td {font-size: 14px !important;}
.same_cell {height: auto;table-layout: fixed;word-break: break-all;}

/* 메인 상단 그리드 */
.cond_status {display: grid;grid-template-columns: 2fr 2.5fr 1fr;}

/* 지하철노선도 */
.map_box {width: 100%;padding: 15px;box-sizing: border-box;border: 3px solid #f0f0f0;
display: flex;align-items: center;justify-content: center;border-radius: 5px;}
.map_box div {width: 100%;}
.map_box img {width: 100%;height: 100%;background: url('img/map_bg.png') repeat #fff;}

.element div div:nth-child(3), .element div div:nth-child(4) {display: none;}

/* 메인 게시판 */
.MuiCardHeader-root{
  padding: 0;
  padding: 9px 0 9px 10px;
}

.MuiTableCell-head, .MuiTableCell-body {
  padding: 6px 0 !important;
  text-align: center;
}

/* 상태 현황 */
.charts_box {
  display: inline-flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: auto;
}

.charts_box > div {
  /* position: relative;
  flex-basis: 22.5%; */
  flex-grow: 1;
  flex-basis: 20%;
}

/* .charts_box > div {
  position: relative;
  flex-basis: 22.5%;
  margin:0 0.7%;
} */

.charts_box div {
  position: relative;
  width: 170% !important;
}

.charts_box svg {
  width: 170% !important;
  margin-left: -12%;
}

/* 파트별 상태현황 */
.part_status {
  margin: 0 auto;
  width: 98%;
  height: auto;
  text-align: center;
  border-spacing: 5px;
  border-collapse: separate;
  table-layout: fixed;
  word-break: break-all;
}

.part_status th {
  color: #fff;
  border-radius: 5px;
}

.part_status td {
  border-radius: 5px;
}

.part_status .train1 {
  background-color: #759cce;
}

.part_status .train2 {
  background-color: #f5a251;
}

.bad th{
  background-color: red;
}

.bad td{
  border: 2px solid red;
}

.insuff th {
  background-color: #e7c748;
}

.insuff td {
  border: 2px solid #e7c748;
}

.communi th {
  background-color: #b2b2b2;
}

.communi td {
  border: 2px solid #b2b2b2;
}

.normal th {
  background-color: #2666ae;
}

.normal td {
  border: 2px solid #2666ae;
}

.fine th {
  background-color: #308a2a;
}

.fine td {
  border: 2px solid #308a2a;
}

.sum th {
  background-color: #1a1a1a;
}

.sum td {
  border: 2px solid #1a1a1a;
}

/* 파트, 역사, 설비 */
.table_part {
  height:100%;
}

.table_part table {
  height: 100%;
  table-layout: fixed;
  word-break: break-all;
}

.table_part table tr td:first-child{
  background-color: #fafafa;
  font-weight: 700;
}

/* 서브페이지 */
.same_cell .icon {
  padding: 5px;
  min-width: auto;
}

.same_cell .icon img {
  width: 25px;
}

/* 반응형 */
@media (max-width: 1700px) {
  .charts_box div {
    width: 180% !important;
  }

  .charts_box svg {
    width: 180% !important;
    margin-left: -40px;
  }
}

@media (max-width: 1580px) {
  .part_status_box{margin: 10px 0;}
  .charts_box div {
    width: 190% !important;
  }

  .charts_box svg {
    width: 190% !important;
    margin-left: -40px;
  }
} 

@media (max-width: 1500px) {
  .cond_status {
    margin-left: 5px;
    grid-template-columns: unset;
    grid-template-rows: 1fr 230px 150px;
  }
  .charts_box div {
    width: 140% !important;
  }
  .charts_box svg {
    width: 100% !important;
    margin-left: -10%;
  }
} 

@media (max-width: 990px) {
  .main_left, .sub_left{padding-right: 0;}
  .map_box {
    display: none;
  }
  .sub_left {
    margin: 0 auto;
    width: 99%;  
  }
  .sub_right, .main_right {
    margin: 0 auto;
    width: 99%;
  }
  .main_right {
    padding-left: 0;
  }
  .cond_status {
    margin-left: 0;
  }
  .part_status th {
    font-size: 0.75rem !important;
  }
  .main_table {
    margin-left: 0;
  }
  /* .charts_box > div {
    position: relative;
    flex-basis: 20%;
    margin:0 0.7%;
  }
  .charts_box > div > div{
    position: absolute;
    width: 150% !important;
    height:150% !important;
    left: -40px;
    top: 0;
  }  */
}

@media (max-width: 800px) {
  .charts_box div {
    width: 170% !important;
  }
  .charts_box svg {
    width: 100% !important;
    margin-left: -12%;
  }
} 